.registrationWrapper {
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  background-color: #0009;
  transition: all .25s ease-in-out;
  position: absolute;
  top: 0;
  overflow: hidden;
}

.registrationWrapper.visible {
  opacity: 1;
  visibility: visible;
  overflow: hidden;
}

.registr-form {
  width: 320px;
  background-color: #212121;
  border-radius: 20px;
  flex-direction: column;
  margin: 0 auto;
  padding: 20px;
  transition: all .4s ease-in-out;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.registr-form.hidden {
  transition: all .4s ease-in-out;
  left: -100%;
}

.form-title {
  text-align: center;
  color: var(--text-color-white);
  letter-spacing: .2em;
  margin-bottom: 40px;
  font-size: 30px;
  font-weight: 600;
}

.input:focus ~ .pass-email-span, .input:valid ~ .pass-email-span {
  color: var(--text-color-white);
  padding: 0 10 px;
  border-left: 1px solid var(--main-accent-color);
  border-right: 1px solid var(--main-accent-color);
  letter-spacing: .2em;
  background: #212121;
  font-size: .65em;
  transform: translateX(10px)translateY(-7px);
}

.input:focus, .input:valid {
  border: 1px solid var(--main-accent-color);
}

.pass-email-span {
  pointer-events: none;
  color: var(--text-color-white);
  text-transform: uppercase;
  padding: 10px;
  font-size: 1em;
  transition: all .25s;
  position: absolute;
  left: 20px;
}

.first-name, .email {
  width: 100%;
  background: inherit;
  color: var(--text-color-white);
  resize: none;
  border: 1px solid #ffffff40;
  border-radius: 5px;
  outline: none;
  margin-bottom: 30px;
  padding: 10px;
  font-size: 1em;
  transition: all .25s;
}

.registr-btn, .login-btn {
  color: #fff;
  cursor: pointer;
  background: #ffffff40;
  border: none;
  border-radius: 4px;
  outline: none;
  margin: 0 auto 40px;
  padding: 10px 50px;
  transition: all .3s;
  display: flex;
  position: relative;
  overflow: hidden;
}

.registr-btn:hover, .login-btn:hover {
  background: #60606040;
}

.registr-text {
  font-size: 16px;
}

.changeForm__text {
  text-align: center;
  color: var(--text-color-white);
  font-size: 18px;
}

.changeForm__span {
  cursor: pointer;
  font-size: 28px;
  transition: all .25s ease-in-out;
}

.changeForm__span:hover, .changeForm__span:focus {
  color: var(--main-accent-color);
  transition: all .25s ease-in-out;
}

.login-form {
  width: 320px;
  background-color: #212121;
  border-radius: 20px;
  flex-direction: column;
  margin: 0 auto;
  padding: 20px;
  transition: all .25s ease-in-out;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.registr-form.registr-form.hidden + .login-form.visible {
  transition: all .25s ease-in-out;
  left: 50%;
}

.loginedModal {
  width: 320px;
  background-color: #212121;
  border-radius: 20px;
  margin: 0 auto;
  padding: 20px;
  transition: all .25s ease-in-out;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loginedWrapper {
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  background-color: #0009;
  transition: all .25s ease-in-out;
  position: absolute;
  top: 0;
  overflow: hidden;
}

.loginedWrapper.active {
  opacity: 1;
  visibility: visible;
}

.exitBtnForm {
  width: 180px;
  height: 25px;
  background-color: var(--main-accent-color);
  border: none;
  border-radius: 20px;
}

.exitBtnForm:hover {
  background-color: var(--background-color);
  transition: all .25s;
}

.hidden {
  overflow: hidden;
}

.locked {
